* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

div.discussion__avatar--wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0 0 0;
    padding: 1rem;
}
img.discussion__avatar--image {
    border-radius: 50%;
    width: 48px;
}

div.discussion__content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
div.discussion__information {
    margin-left: auto;
}
div.discussion__answered {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0 0 0;
    padding: 1rem;
}
.discussion__container{
    display: flex;
}
a{
    text-decoration: none; /* 링크의 밑줄 제거 */ 
    color: inherit;
}
div.disussion_input{
    display: flex;
    flex-direction: column;
}